<template>
    <div>
        <van-nav-bar
	        title="找回密码"
            left-text="返回"
	        left-arrow 
          @click-left="$router.go(-1)"
	    />

        <van-form @submit="commit">
            <van-field
                v-model="lists.phone"
                name="手机号"
                label="手机号"
                placeholder="请输入手机号"
            >
                <template v-slot:left-icon>
                    <i class="fa fa-phone fa-lg" aria-hidden="true"></i>
                </template>
            </van-field>
            <van-field
                v-model="lists.password"
                :type="ishas?'password':'text'"
                name="新密码"
                label="新密码"
                placeholder="密码（至少6位，且开头为字母）"
                @click-right-icon="viewPassword"
            >
                <template v-slot:left-icon>
                    <i class="fa fa-unlock-alt fa-lg" aria-hidden="true"></i>
                </template>
                <template v-slot:right-icon>
                    <i class="fa fa-lg" :class="ishas?'fa-eye-slash':'fa-eye'" aria-hidden="true"></i>
                </template>
            </van-field>
            <div style="margin: 16px;">
                <van-button round block native-type="submit" >
                提交
                </van-button>
            </div>
        </van-form>
    </div>
</template>

<script>
import ValidForm from '../assets/js/validForm'
    export default {
        data(){
            return {
                lists:{
                    phone:'',
                    password:''
                },
                //false: 闭合，true：睁开
                ishas: true,
            }
        },
        methods:{

            //切换密码的格式
            viewPassword() {
                this.ishas = !this.ishas;
            },
            //提交新密码
            commit(){
                if (!ValidForm.validUserForm(this.lists)) {
                    return;
                }

                //发起发送验证码请求
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: "POST",
                    url: "/retrievePassword",
                    data: {
                        appkey: this.appkey,
                        phone:this.lists.phone,
                        password:this.lists.password
                    }
                })
                    .then(result => {
                    this.$toast.clear();
                    
                    if (result.data.code == 'L001') {
                        this.$router.push({name:'login'})
                    } else {
                        this.$toast(result.data.msg);
                    }
                    
                    })
                    .catch(() => {
                    this.$toast.clear();
                    
                    
                    });
            }
                
            
        }
    }
</script>

<style lang="less" scoped>
    @c:#f80;
    /deep/ .van-nav-bar__text {
    color: @c;
    }
    /deep/ .van-icon-arrow-left {
    color: @c;
    }
    //更改提交按钮颜色
    .van-button {
    background-color: @c;
    border: none;
    color: #fff;
    }
</style>